import {postRequest,putRequest,deleteRequest,getRequest} from "../utils/request";

export const listHealthInfo = (params) => {
    return postRequest("/uiapi/v1/health/list", params)
}

export const obtainHealthInfoByUserId = (userId) => {
    return getRequest("/uiapi/v1/health/obtain/detail/"+ userId,null)
}


export const createHealthInfo = (params) => {
    return postRequest("/uiapi/v1/health/add", params)
}

export const deleteHealthInfo = (params) => {
    return deleteRequest("/uiapi/v1/health/delete", params)
}

export const updateHealthInfo = (params) => {
    return putRequest("/uiapi/v1/health/update", params)
}