<template>
    <div style="margin-top: 40px">
        <el-form ref="form" :model="formData" label-width="380px">
            <el-form-item label="用户名称">
                <el-input v-model="formData.username" style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item label="身份证号">
                <el-input v-model="formData.idCard" style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item label="过去14天是否与确诊病人、疑似病人、或无症状感染者接触">
                <el-radio-group v-model="formData.isTouch" style="width: 350px">
                    <el-radio :label="false">否</el-radio>
                    <el-radio :label="true"> 是</el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="当前是否与居家隔离人员同居">
                <el-radio-group v-model="formData.isLive" style="width: 350px">
                    <el-radio :label="false">否</el-radio>
                    <el-radio :label="true"> 是</el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="当前状态">
                <el-radio-group v-model="formData.status" style="width: 500px">
                    <el-radio :label="item.value" :value="item.value" v-for="item in statusList" style="margin-top: 10px">{{item.name}}</el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="当前症状">
                <el-checkbox-group :min="1" v-model="formData.currentSymptom" style="width: 350px">
                    <el-checkbox v-for="item in symptomList" :label="item.value" :key="item.value">{{item.name}}</el-checkbox>
                </el-checkbox-group>
            </el-form-item>

            <el-form-item label="当前体温">
                <el-input v-model="formData.currentHeat" style="width: 350px"></el-input>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click="onSubmit">保存</el-button>
                <el-button>取消</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import {createHealthInfo,obtainHealthInfoByUserId,updateHealthInfo}  from "../../../api/healthInfo"
    export default {
        name: "info",
        data() {
            return {
                checkedCities: null,
                formData: {
                    id: null,
                    userId: JSON.parse(window.sessionStorage.getItem('user')).userId,
                    username: JSON.parse(window.sessionStorage.getItem('user')).username,
                    idCard: JSON.parse(window.sessionStorage.getItem('user')).idCard,
                    isTouch: null,
                    isLive: null,
                    status: null,
                    currentSymptom: [],
                    currentHeat: null
                },
                statusList: [
                    {
                        "name": "正常",
                        "value": 0
                    },
                    {
                        "name": "居家隔离医学观察",
                        "value": 1
                    },
                    {
                        "name": "集中隔离医学观察",
                        "value": 2
                    },
                    {
                        "name": "隔离治疗",
                        "value": 3
                    },
                    {
                        "name": "隔离治疗后出院",
                        "value": 4
                    },
                    {
                        "name": "其他",
                        "value": 5
                    }
                ],
                symptomList: [
                    {
                        "name": "没有出现症状",
                        "value": "0"
                    },
                    {
                        "name": "感冒症状、乏力、咳嗽、发烧、肌肉痛、头痛",
                        "value": "1"
                    },
                    {
                        "name": "踹憋、呼吸急促",
                        "value": "2"
                    },
                    {
                        "name": "恶心呕吐、腹泻",
                        "value": "3"
                    },
                    {
                        "name": "心慌、胸闷",
                        "value": "4"
                    },
                    {
                        "name": "结膜炎",
                        "value": "5"
                    },
                    {
                        "name": "其他",
                        "value": "6"
                    }
                ],
                isValues:[0,1]
            }
        },
        created() {
            this.obtainHealthInfo()
        },
        methods: {

            obtainHealthInfo() {
                // debugger
                obtainHealthInfoByUserId(this.formData.userId).then(res => {
                    if (res.data != null && res.data != "" ) {
                        this.formData = res.data
                    }
                })

            },

            onSubmit() {
                console.log(this.formData)
                if (this.formData.id == null) {
                    createHealthInfo(this.formData).then(res => {
                        this.$message({
                            message: '创建成功',
                            type: 'success'
                        });
                        this.obtainHealthInfo()
                    })
                } else {
                    updateHealthInfo(this.formData).then(res => {
                        this.$message({
                            message: '更新成功',
                            type: 'success'
                        });
                        this.obtainHealthInfo()
                    })
                }
            }
        }
    }
</script>

<style scoped>

</style>